<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="5">
                        <div style="font-size: 20px;height: 37px">
                            iTalkList
                        </div>
                    </el-col>
                </el-row>

                <filter-list listType="talk" :filter-list="filterList" :label-list="labelList"/>

                <lesson-list lesson-type="talk" :type-list="{}" :lesson-list="talkList"
                             edit-page="/showITalkInfo/" @delLessonInfo="delLessonInfo" @handleCommand="handleCommand" @releaseTimer="releaseTimer"/>
            </el-main>
            <el-footer style="text-align: center">
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="paginationSize"
                        layout="total, prev, pager, next"
                        :total="paginationNum">
                </el-pagination>
            </el-footer>

            <release-timing :isShow="isShowSetTime" :id="currentId" @hideTiming="isShowSetTime = false"  @releaseTimer="releaseTimer"/>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'
    import LessonList from '../components/LessonListItem'

    export default {
        components: {
            FilterList,
            ReleaseTiming,
            LeftAside,
            LessonList
        },
        data() {
            return {
                talkList: [],
                multipleSelection: [],
                currentPage: 1,
                isShowSetTime: false,
                filterList: {},
                paginationNum: 0,
                paginationSize: 10,
                labelList:[],
                currentId:'',
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.getTalkListData();
                this.getTalkNumData();
                this.getAllLabelData();
            },
            getTalkListData() {
                this.$axios.get('/italk/_find', {
                    params: {
                        page: this.currentPage,
                        num: this.paginationSize,
                        category: 'iTalk'
                    }
                }).then((res) => {
                    console.log(res)
                    if (!res.data.code) {
                        this.talkList = res.data.data
                    }
                })
            },
            getTalkNumData() {
                this.$axios.get('/italk/_getnum', {
                    params: {
                        page: this.currentPage,
                        category: 'iTalk'
                    }
                }).then((res) => {
                    this.paginationNum = res.data.data;
                })
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getTalkListData();
            },
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                    this.currentId = command.value._id;
                } else {
                    this.postUpdateTalkData(command.value);
                }
            },
            postUpdateTalkData(command) {
                let data = new FormData();
                data.append('lessonId', command._id);
                data.append('publicTime', parseInt(new Date().getTime() / 1000));
                data.append('status', 2);

                this.$axios.post('/italk/_update', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.init();
                    }
                })
            },
            delLessonInfo(id){
                this.$confirm('文件将被直接删除', '确定删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    confirmButtonClass: 'el-button el-button--danger'
                }).then(() => {
                    this.delLessonData(id)
                }).catch(() => {
                    // this.$message({type: 'info', message: '已取消删除'});
                });
            },
            delLessonData(id) {
                this.$axios.get('/italk/_delete', {params: {lessonId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.$message({type: 'success', message: '已删除'});
                        this.init();
                    }
                })
            },
            getAllLabelData() {
                this.$axios.get('/label/_find', {params: {category: 'iTalk', all: 'all'}}).then((res) => {
                    console.log(res.data.data)
                    if (!res.data.code) {
                        this.labelList = res.data.data
                    }
                })
            },
            hideTimer() {
                this.isShowSetTime = false;
                this.init();
            },
            releaseTimer(data){
                this.$axios.post('/italk/_public', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('设置成功');
                        this.hideTimer();
                    }
                })
            }
        },
    }
</script>

<style scoped>
    .el-dropdown-link {
        cursor: pointer;
        color: #409eff;
        font-size: 12px
    }

    .dropdown-edit {
        margin-left: 15px
    }
</style>