<template>
    <div class="media">
        <div v-if="currentType === 'mp4'||currentType === '3u8'" class="media">
            <video controls="" preload="none" poster="" class="media">
                <source :src="fileInfo.fileUrl" type="video/mp4">
            </video>
        </div>

        <div v-if="currentType === 'jpg'|| currentType === 'png'||currentType === 'gif'" class="media">
            <img :src="fileInfo.fileUrl" class="media"/>
        </div>

        <div v-if="currentType === 'mp3'" class="media">
            <audio controls="" preload="none" class="media">
                <source :src='fileInfo.fileUrl' type="audio/mp3">
            </audio>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            currentType: String,
            fileInfo: Object,
        },
        mounted(){
        },
        watch:{
            currentType(newType){
                console.log(newType)
            },
        }
    }
</script>

<style scoped>
    .media {
        width: 100%;
        height: 100%;
        min-height: 60px;
    }
</style>