<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="12">
                        {{talkInfo.materialId.zhTitle}}
                    </el-col>
                    <div style="float: right">
                        <div>
                            <el-dropdown split-button type="primary" size="small" @command="handleCommand"
                                         @click="postReleaseData" trigger="click">发布
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'time',value:''}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                </el-row>

                <el-row class="talkInfo">
                    <el-col :span="3">

                        <div>难度</div>

                        <div>英文字幕</div>

                        <div>标签</div>
                    </el-col>
                    <el-col :span="12">
                        <div>
                            <span v-if="talkInfo.materialId.difficulty === 'simple'">初级</span>
                            <span v-if="talkInfo.materialId.difficulty === 'easily'">中级</span>
                            <span v-if="talkInfo.materialId.difficulty === 'difficulty'">高级</span>
                            <span v-if="talkInfo.materialId.difficulty === 'bonus'">彩蛋题</span>
                        </div>
                        <div>
                            {{talkInfo.materialId.enVvt || '--'}}
                        </div>
                        <div>{{talkInfo.materialId.label || '--'}}</div>
                    </el-col>
                </el-row>

                <show-media :file-info="fileInfo" :current-type="currentType" style="width: 732px;height: 403px"/>

                <release-timing :isShow="isShowSetTime" :id="$route.params.id" @hideTiming="hideTimer"
                                @releaseTimer="releaseTimer"/>

            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'
    import ShowMedia from '../components/ShowMediaItem'

    export default {
        components: {
            ReleaseTiming,
            LeftAside,
            ShowMedia
        },
        data() {
            return {
                isShowSetTime: false,
                currentType: '',
                talkInfo: {
                    materialId: {}
                },
                url: {
                    ShowIFunInfo: '/ifun/',
                    ShowITalkInfo: '/italk/'
                },
                fileInfo: {},
                mediaInfo: {},
            }
        },
        mounted() {
            this.getOneTalkData();
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
            getOneTalkData() {
                this.$axios.get(this.url[this.$route.name] + '_getone', {params: {lessonId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res.data.data)
                        this.currentType = res.data.data.materialId.fileUrl.substring(res.data.data.materialId.fileUrl.length - 3);
                        this.fileInfo = res.data.data.materialId;
                        this.talkInfo = res.data.data;
                    }
                })
            },
            hideTimer() {
                this.isShowSetTime = false;
            },
            postReleaseData() {
                let data = new FormData();
                data.append('lessonId', this.$route.params.id);
                data.append('publicTime', parseInt(new Date().getTime() / 1000));
                data.append('status', 2);

                this.$axios.post(this.url[this.$route.name] + '_update', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('发布成功');
                    }
                })
            },
            releaseTimer(data) {
                this.$axios.post(this.url[this.$route.name] + '_public', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('设置成功');
                        this.hideTimer()
                    }
                })
            }
        },
    }
</script>

<style scoped>
    .talkInfo {
        font-size: 12px;
        padding: 20px 0;
    }

    .talkInfo div {
        margin: 15px 0;
    }
</style>