<template>
    <el-container>
        <media-aside :file-info="fileInfo"/>
        <el-container>
            <!--<el-header></el-header>-->

            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="3">
                        <div>基本信息</div>
                    </el-col>
                    <el-col :span="5">
                        <div>
                            <el-button size="small" @click="postCreateCMSData">生成CMS文章</el-button>

                            <el-button type="primary" size="small" @click="postMediaInfoData()">保存</el-button>
                        </div>
                    </el-col>
                </el-row>


                <div style="margin: 40px 0">
                    <span class="typeTitle">类型</span>
                    <el-select placeholder="请选择" v-model="currentType" value="" size="mini">
                        <el-option label="iFun" value="iFun"/>
                        <el-option label="iTalk" value="iTalk"/>
                        <el-option label="iListen" value="iListen" disabled/>
                        <el-option label="iWords" value="iWord" disabled/>
                    </el-select>
                </div>

                <talk-info ref="talkForm" :type="currentType" :form="iTalkForm" :label-list="labelList"/>

                <fun-info ref="funForm" :type="currentType" :form="iFunForm" :label-list="labelList"/>
            </el-main>


            <!--<el-footer></el-footer>-->
        </el-container>
    </el-container>
</template>

<script>
    import TalkInfo from '../components/TalkInfoItem'
    import FunInfo from '../components/FunInfoItem'
    import MediaAside from '../components/MediaAsideItem'

    export default {
        name: "edit-media-info",
        components: {
            TalkInfo,
            FunInfo,
            MediaAside
        },
        data() {
            return {
                currentType: 'iFun',
                iTalkForm: {
                    // zhTitle:'',
                    // zhSubtitle:'',
                    // enSubtitle:'',
                    // currentLabel:'',
                    // currentDifficulty:''
                },
                iFunForm: {
                    // zhTitle:'',
                    // enTitle:'',
                    // abstract:'',
                    // currentLabel:'',
                    // zhSubtitle:'',
                    // enSubtitle:'',
                    // currentType:'',
                    // currentDifficulty:''
                },
                labelList: [],
                fileInfo: {},
            }
        },
        mounted() {
            this.getOneMediaData();
        },
        methods: {
            postCreateCMSData() {
                this.checkFormRules().then((result) => {
                    let data = this.getFormData(this.getMediaInfo());
                    this.$axios.post('/material/_createCMS', data).then((res) => {
                        if (!res.data.code) {
                            this.$message.success('创建成功');
                            this.postMediaInfoData(data);
                        }
                    });
                });
            },
            checkFormRules() {
                return new Promise((resolve, reject) => {
                    if (this.currentType === 'iFun') {
                        this.$refs.funForm.submitForm('ruleForm').then((result) => {
                            resolve(result);
                        })
                    } else if (this.currentType === 'iTalk') {
                        this.$refs.talkForm.submitForm('ruleForm').then((result) => {
                            resolve(result);
                        })
                    }
                });
            },
            postAddLabelData(list) {
                list = list || [];
                list.forEach((val) => {
                    let data = new FormData();
                    data.append('label', val);
                    data.append('category', this.currentType);

                    this.$axios.post('/label/_add', data).then((res) => {
                        console.log(res)
                    });
                })
            },
            getOneMediaData() {
                this.$axios.get('/material/_getone', {params: {materialId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res)
                        this.getMediaFormInfo(res);
                        this.fileInfo = res.data.data;
                    }
                })
            },
            getMediaFormInfo(res) {
                if (res.data.data.category) {
                    this.currentType = res.data.data.category;
                    res.data.data.label = res.data.data.label || [];
                    switch (res.data.data.category) {
                        case 'iFun':
                            this.iFunForm = res.data.data;
                            break;
                        case 'iTalk':
                            this.iTalkForm = res.data.data;
                            break;
                    }
                }
                this.getAllLabelData(res.data.data.category);
            },
            getMediaInfo() {
                let form = this.iFunForm;
                switch (this.currentType) {
                    case 'iFun':
                        form = this.iFunForm;
                        break;
                    case 'iTalk':
                        form = this.iTalkForm

                }
                return form;
            },
            getAllLabelData(type) {
                this.$axios.get('/label/_find', {params: {category: type, all: 'all'}}).then((res) => {
                    console.log(res.data.data)
                    if (!res.data.code) {
                        this.labelList = res.data.data
                    }
                })
            },
            getFormData(data) {
                let formData = new FormData();
                data.materialId = this.$route.params.id;
                data.source = 'upload';
                data.status = 0;
                data.category = this.currentType;
                delete data.fileId;
                delete data._id;
                Object.keys(data).forEach(value => {
                    formData.append(value, data[value])
                });
                formData.set('label', JSON.stringify(data.label));
                return formData
            },
            postMediaInfoData(data) {
                this.checkFormRules().then((result) => {
                    let formData = data || this.getFormData(this.getMediaInfo());
                    this.postAddLabelData(this.getMediaInfo().label);

                    this.$axios.post('/material/_update', formData).then((res) => {
                        if (!res.data.code) {
                            this.$message.success('更新成功');
                        }
                    })
                });
            },
        },
        watch: {
            currentType(newType) {
                console.log(newType)
                this.getAllLabelData(newType);
            }
        }
    }
</script>

<style scoped>
    .sourceInfo {
        color: rgba(136, 136, 136, 1);
        font-size: 14px;
        text-align: left;
        margin: 20px 0;
    }

    .typeTitle {
        margin-right: 15px;
        font-size: 14px;
    }
</style>