                            自动切分为iTalk
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>每一句</el-dropdown-item>
                            <el-dropdown-item>每二句</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>

                    <el-button type="primary" size="mini" style="float: right" v-show="isShowCut">切分</el-button>
                </div>

                <div class="cutList">
                    <div class="manual" v-show="manualCut">
                        <div class="manualTitle">手动裁剪</div>

                        <el-row :gutter="20" class="manualList">
                            <el-col :span="1">
                                <div>
                                    <i class="el-icon-caret-right" style="font-size: 27px"></i>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div style="display: inline-block">
                                    <el-input size="mini" placeholder="" style="width: 40%" />
                                    -
                                    <el-input size="mini" placeholder="" style="width: 40%" />
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div>
                                    <el-input size="mini" placeholder="" />
                                </div>
                            </el-col>
                            <el-col :span="2">
                                <div>
                                    <el-input size="mini" placeholder="" />
                                </div>
                            </el-col>
                            <el-col :span="2">
                                <div>
                                    <i class="el-icon-delete" style="font-size: 25px"></i>
                                </div>
                            </el-col>
                        </el-row>
                    </div>

                    <div v-show="autoCut">
                        <div class="manualTitle">自动切分iTalk (42个片段)</div>

                        <el-row :gutter="20" class="manualList">
                            <el-col :span="1">
                                <div>
                                    <i class="el-icon-caret-right" style="font-size: 27px"></i>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div style="display: inline-block">
                                    <el-input size="mini" placeholder="" style="width: 40%" />
