                            <el-col :span="2">
                                <div>
                                    <el-input size="mini" placeholder="" />
                                </div>
                            </el-col>
                            <el-col :span="2">
                                <div>
                                    <i class="el-icon-delete" style="font-size: 25px"></i>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20" class="manualList">
                            <el-col :span="1">
                                <div>
                                    <i class="el-icon-caret-right" style="font-size: 27px"></i>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div style="display: inline-block">
                                    <el-input size="mini" placeholder="" style="width: 40%" />
