                                    <el-input size="mini" placeholder="" />
                                </div>
                            </el-col>
                            <el-col :span="2">
                                <div>
                                    <i class="el-icon-delete" style="font-size: 25px"></i>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </div>
            </el-main>


            <!--<el-footer></el-footer>-->
        </el-container>
    </el-container>
</template>

<script>
    import MediaAside from '../components/MediaAsideItem'
    import ShowMedia from '../components/ShowMediaItem'

    export default {
        name: "edit-media-info",
        components: {
            MediaAside,
            ShowMedia
        },
        data() {
            return {
                isShowCut: false,
                manualCut:false,
                autoCut:false,
                fileInfo:{},
                currentType:'',
            }
        },
        mounted(){
            this.getOneMediaData();
        },
        methods:{
            getOneMediaData() {
                this.$axios.get('/material/_getone', {params: {materialId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res)
                        this.fileInfo = res.data.data;
                        this.currentType = res.data.data.fileUrl.substring(res.data.data.fileUrl.length - 3);
                    }
                })
            },
        }
    }
</script>

<style scoped>
    .cutList {
        padding: 40px 0;
    }

    .videoInfo {
        margin: 40px 0;
    }

    .addManual {
        margin-right: 15px;
    }

    .manual {
        margin-bottom: 40px;
    }

    .manualTitle {
        color: rgba(168, 168, 168, 1);
        font-size: 12px;
        text-align: left;
    }
    .manualList{
        margin-top: 10px;
    }
</style>