<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row>
                    <el-col :span="8">
                        <el-input placeholder="标题" size="medium" v-model="iWordTitle"/>
                    </el-col>
                    <el-col :span="4" :offset="1">
                        <el-select placeholder="分类" v-model="iWordType" value="" size="medium">
                            <el-option
                                    v-for="(index,value) in iWordTypeList"
                                    :key="value"
                                    :label="index"
                                    :value="value">
                            </el-option>
                        </el-select>
                    </el-col>
                    <div style="float: right">
                        <div>
                            <el-button size="small" style="margin-right: 15px" @click="postWordsData">保存
                            </el-button>

                            <el-dropdown split-button type="primary" size="small" @click="postReleaseData"
                                         @command="handleCommand"
                                         trigger="click">发布
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'time',value:''}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                </el-row>


                <el-row type="flex" class="workInfoMain">
                    <el-col :span="11">
                        <p>Markdown
                            <el-button type="text" style="margin-left: 30px;padding: 0"
                                       @click="dialogTableVisible = true">插入媒体
                            </el-button>
                        </p>
                        <el-input id="markdownInfo" type="textarea" @change="changeMarkDown" v-model="markdownInfo"
                                  :style="{height: currentHeight + 'px'}"/>
                    </el-col>
                    <el-col :span="11" :offset="1">
                        <p>渲染预览</p>
                        <div class="workInfoShow"
                             :style="{height: currentHeight + 'px'}">
                            <vue-markdown :source="showInfo" class="markdownInfo">
                            </vue-markdown>
                        </div>
                    </el-col>
                </el-row>

                <release-timing :isShow="isShowSetTime" :id="currentId" @hideTiming="hideTimer"
                                @releaseTimer="releaseTimer"/>
            </el-main>


            <el-dialog title="媒体库" width="95%" top="5vh" :before-close="hideMediaDia"
                       :visible.sync="dialogTableVisible">
                <filter-list listType="media" :filter-list="filterList" @filterMediaList="filterMediaList"/>

                <div class="am-form-file">
                    <el-button size="mini" type="primary" style="float: left">上传</el-button>

                    <input type="file" multiple ref="myFiles" @change="changeUploadList"
                           accept="audio/mp3,video/mp4,image/jpg,image/png"/>
                </div>

                <media-list ref="mediaListDia" table-type="mediaDia" :new-row-list="newRowList" :media-list="mediaList"
                            @select="handleSelectChange"
                            :current-page="currentPage"/>

                <span slot="footer" class="dialog-footer">
                    <el-pagination style="text-align: center"
                                   @current-change="handleCurrentChange"
                                   :current-page.sync="currentPage"
                                   :page-size="paginationSize"
                                   layout="total, prev, pager, next"
                                   :total="paginationNum">
                    </el-pagination>
                    <el-button type="primary" size="small" @click="createMDInfo">确 定</el-button>
                </span>
            </el-dialog>

            <el-dialog
                    title="上传文件"
                    :visible.sync="dialogUploadVisible"
                    :close-on-press-escape="false"
                    :close-on-click-modal="false"
                    :before-close="hideUploadDia"
                    width="50%" class="dialogUpload">
                <div style="border: 1px solid #bbb;min-height: 200px">
                    <div style="padding: 10px;">
                        <div v-for="(i,index) in fileList" :key="index">
                            <el-row type="flex" justify="space-between">
                                <el-col :span="18">
                                    <div>
                                        <span style="font-size: 12px">{{i.name}}</span>
                                        <el-progress :stroke-width="3" :percentage="i.num"
                                                     :status="i.num === 100 ? 'success' : 'text'">{{i.num}}%
                                        </el-progress>
                                    </div>
                                </el-col>
                                <el-col :span="4">
                                    <div>
                                        <el-button v-if="i.id!==''" type="text" @click="jumpNewPage(i.id)"
                                                   icon="el-icon-edit">查看
                                        </el-button>
                                    </div>
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </div>
                <span slot="footer">
                        <el-button type="primary" @click="hideUploadDia">完成</el-button>
                </span>
            </el-dialog>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import MediaList from '../components/MediaListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'

    export default {
        components: {
            ReleaseTiming,
            LeftAside,
            FilterList,
            MediaList,
        },
        data() {
            return {
                iWordTypeList: {
                    lifeStyle: '有型生活',
                    oversea: '海外生存',
                    date: '约会妙计',
                    tourGuide: '旅游指南',
                    serious: '正经一下',
                    series: '电影美剧',
                    dailyWord: '每日一词'
                },

                iWordTitle: '',
                markdownInfo: '',
                currentHeight: 0,
                showInfo: '',
                isShowSetTime: false,
                dialogTableVisible: false,
                dialogUploadVisible: false,
                filterList: {},
                iWordType: '',
                currentId: this.$route.params.id,

                mediaList: [],
                currentPage: 1,
                paginationNum: 0,
                paginationSize: 10,
                currentSelect: [],
                fileList: [],
                newRowList: [],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.currentHeight = window.innerHeight - 150;
                if (this.$route.params.id !== 'new') {
                    this.getOneWordsData();
                }
                this.getMediaList();
            },
            getMediaList(find) {
                this.getMediaListData(find).then((result) => {
                    this.mediaList = result;

                    this.getMediaNumData(find);
                });
            },
            getMediaListData(find) {
                find = find ? find : {};
                find.page = this.currentPage;
                find.num = this.paginationSize;

                return new Promise((resolve, reject) => {
                    this.$axios.get('/material/_find', {params: find}).then((res) => {
                        if (!res.data.code) {
                            resolve(res.data.data)
                        }
                    });
                })
            },
            getMediaNumData(find) {
                find = find ? find : {};
                this.$axios.get('/material/_getnum', {params: find}).then((res) => {
                    this.paginationNum = res.data.data;
                })
            },
            changeUploadList() {
                let fileList = Object.values(this.$refs.myFiles.files);

                fileList.forEach(value => {
                    this.fileList.push({name: value.name, num: 0, id: ''});
                });
                this.postUploadData(0, fileList);
            },
            handleUploadChange() {
                this.dialogUploadVisible = true;
            },
            postUploadData(num, fileList) {
                let data = new FormData();
                data.append('file', fileList[num]);
                this.handleUploadChange();

                this.$axios.post('/file/_upload', data, {
                    onUploadProgress: function (progressEvent) {
                        this.fileList[num].num = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total));
                    }.bind(this)
                }).then((res) => {
                    if (res.data.code === 0 && res.data.data[0]) {
                        this.$message.success('上传成功');
                        this.fileList[num].id = res.data.data[0].material._id;
                        this.getMediaListData().then((result => {
                            this.mediaList = result;
                            setTimeout(() => {
                                this.newRowList.push(res.data.data[0].material.fileId);
                                this.$refs.mediaListDia.testToggleSelection(this.newRowList);
                            }, 0)
                        }));

                        num += 1;
                        if (num <= fileList.length - 1) {
                            this.postUploadData(num, fileList);
                        } else {
                            this.$refs.myFiles.value = '';
                        }
                    }
                })
            },
            jumpNewPage(key) {
                window.open('#/editMediaInfo/' + key, '_blank')
            },
            hideTimer(val) {
                if (typeof val === 'object') {
                    this.postWordData(this.getAddWordInfo(0)).then((result) => {
                        this.currentId = result.id;
                        this.setScheduledRelease(result._id, val.date);
                    });
                } else {
                    this.postUpdateWordsData();
                    this.isShowSetTime = false;
                }
            },
            setScheduledRelease(id, timestamp) {
                let data = new FormData();
                data.append('lessonId', id);
                data.append('time', timestamp / 1000);

                this.$axios.post('/lesson/_public', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('设置成功');
                        this.jumpPage('/editIWordInfo/' + id);
                        this.isShowSetTime = false;
                    }
                })
            },
            hideUploadDia() {
                this.fileList = [];
                this.dialogUploadVisible = false
            },
            hideMediaDia() {
                this.$refs.mediaListDia.clearSelect();
                this.newRowList = [];
                this.dialogTableVisible = false;
            },
            getOneWordsData() {
                this.$axios.get('/lesson/_getone', {params: {lessonId: this.$route.params.id}}).then((res) => {
                    if (!res.data.code) {
                        console.log(res.data.data)
                        this.iWordTitle = res.data.data.title || '';
                        this.iWordType = res.data.data.class || '';
                        this.markdownInfo = res.data.data.cms;
                    }
                })
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getMediaList();
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
            filterMediaList() {

            },
            postWordsData() {
                if (this.$route.params.id === 'new') {
                    this.postAddWordsData(0);
                } else {
                    this.postUpdateWordsData();
                }
            },
            getAddWordInfo(isRelease) {
                let data = new FormData();
                data.append('cms', this.markdownInfo);
                data.append('zhTitle', this.iWordTitle);
                data.append('category', 'iWord');
                data.append('status', isRelease);
                data.append('class', this.iWordType);
                if (isRelease === 2) {
                    data.append('publicTime', parseInt(new Date().getTime() / 1000));
                }
                return data;
            },
            postAddWordsData(isRelease) {
                this.postWordData(this.getAddWordInfo(isRelease)).then((result) => {
                    this.$message.success('添加成功');
                    this.jumpPage('/editIWordInfo/' + result._id);
                });
            },
            postWordData(data) {
                return new Promise((resolve, reject) => {
                    this.$axios.post('/lesson/_add', data).then((res) => {
                        console.log(res);
                        if (!res.data.code) {
                            resolve(res.data.data)
                        }
                    })
                })
            },
            postUpdateWordsData() {
                let data = new FormData();
                data.append('lessonId', this.$route.params.id);
                data.append('cms', this.markdownInfo);
                data.append('zhTitle', this.iWordTitle);
                data.append('class', this.iWordType);

                this.$axios.post('/lesson/_update', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.$message.success('更新成功');
                    }
                })
            },
            postReleaseData() {
                if (this.$route.params.id === 'new') {
                    return this.postAddWordsData(2)
                }
                let data = new FormData();
                data.append('lessonId', this.$route.params.id);
                data.append('cms', this.markdownInfo);
                data.append('zhTitle', this.iWordTitle);
                data.append('publicTime', parseInt(new Date().getTime() / 1000));
                data.append('class', this.iWordType);
                data.append('status', 2);

                this.$axios.post('/lesson/_update', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('发布成功');
                    }
                })
            },
            jumpPage(key) {
                this.$router.push(key)
            },
            handleSelectChange(val) {
                let all = [];
                val.forEach(value => {
                    all.push(this.getOneFile(value.fileId));
                });

                Promise.all(all).then((result) => {
                    console.log(result)
                    this.currentSelect = result;
                })
            },
            getOneFile(id) {
                return new Promise((resolve, reject) => {
                    this.$axios.get('/file/_getone', {params: {fileId: id}}).then((res) => {
                        if (!res.data.code) {
                            resolve(res.data.data);
                        }
                    })
                });
            },
            releaseTimer(data) {
                this.$axios.post('/lesson/_public', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('设置成功');
                        this.hideTimer(data)
                    }
                })
            },
            createMDInfo() {
                let myField = document.querySelector('#markdownInfo');
                let endPos = myField.selectionEnd;
                myField.focus();

                let str = '';
                this.currentSelect.forEach(value => {
                    let file = value.fileName.substring(value.fileName.length - 4);
                    if (file.toLowerCase() === '.mp3') {
                        str += '\n![audio](' + value.fileUrl + ')\n';
                    } else if (file.toLowerCase() === '.mp4') {
                        str += '\n![video](' + value.fileUrl + ')\n';
                    } else if (file.toLowerCase() === '.jpg' || file.toLowerCase() === '.png' || file.toLowerCase() === '.gif') {
                        str += '\n![pic](' + value.fileUrl + ')\n';
                    }
                });

                this.markdownInfo = this.markdownInfo.substr(0, endPos) + str + this.markdownInfo.substr(endPos, this.markdownInfo.length - 1);
                this.hideMediaDia();
            },

            changeMarkDown() {
                let info = this.markdownInfo;
                let audioList = info.match(/!+\[audio]\(([^)]*)\)/gi) || [];
                audioList.forEach((audio) => {
                    let path = audio.split(/\(|\)/)[1];
                    let audioHtml = `<audio  controls="" preload="none"><source src="${path}"></audio>`;
                    info = info.replace(audio, audioHtml)
                });


                let videoList = info.match(/!+\[video]\(([^)]*)\)/gi) || [];
                videoList.forEach((video) => {
                    let path = video.split(/\(|\)/)[1];
                    let videoHtml = `<video  controls="" preload="none"><source src="${path}" type="video/mp4"></video>`;
                    info = info.replace(video, videoHtml)
                });
                this.showInfo = info;
            },
        },
        watch: {
            markdownInfo(newInfo, old) {
                this.changeMarkDown();
            }
        }
    }
</script>

<style scoped>
    .workInfoMain {
        margin-top: 46px;
        font-size: 13px
    }

    .workInfoMain p {
        margin-bottom: 8px;
    }

    .workInfoShow {
        overflow-y: auto;
        border: 1px solid #dcdfe6;
        border-radius: 5px;
        min-height: 20px;
    }

    .markdownInfo {
        padding: 5px 15px;
    }

    .am-form-file {
        position: relative;
        overflow: hidden;
    }

    .am-form-file input[type=file] {
        position: absolute;
        z-index: 1;
        left: 0;
        opacity: 0;
        width: 56px;
        height: 28px;
        cursor: pointer;
    }
</style>