<template>
    <div>
        <el-table
                ref="multipleTable"
                :data="mediaList"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectChange">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="zhTitle"
                    label="标题">
                <template slot-scope="scope">
                    <el-popover
                            placement="right"
                            width="400"
                            trigger="hover">
                        <show-media :file-info="scope.row" :current-type="scope.row.fileUrl.substring(scope.row.fileUrl.length - 3)" style="width: 100%;height: 100%" />
                        <div slot="reference" :title="scope.row.zhTitle"
                             class="table-primary">
                            {{scope.row.zhTitle}}
                        </div>
                    </el-popover>

                </template>
            </el-table-column>
            <el-table-column
                    prop="category"
                    label="类型">
            </el-table-column>
            <el-table-column
                    prop="difficulty"
                    label="难度">
                <template slot-scope="scope">
                    <span v-if="scope.row.difficulty === 'simple'">初级</span>
                    <span v-if="scope.row.difficulty === 'easily'">中级</span>
                    <span v-if="scope.row.difficulty === 'difficulty'">高级</span>
                    <span v-if="scope.row.difficulty === 'bonus'">彩蛋题</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作" v-if="tableType === 'media'">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="jumpPage('/editMediaInfo/'+scope.row._id)">编辑
                    </el-button>
                    <el-button type="text" size="small" style="color: #F56C6C;" @click="delMediaInfo(scope.row._id)">
                        刪除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import ShowMedia from './ShowMediaItem'

    export default {
        props: {
            tableType: String,
            mediaList: Array,
            newRowList: Array,
        },
        components: {
            ShowMedia
        },
        data() {
            return {}
        },
        mounted() {
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            testToggleSelection(rows) {
                console.log(rows);
                let arr = [];
                this.mediaList.forEach((value) => {
                    if (rows.indexOf(value.fileId) !== -1) {
                        console.log(value);
                        arr.push(value)
                    }
                });

                this.toggleSelection(arr)
            },
            delMediaInfo(id) {
                this.$emit('delMediaInfo', id)
            },
            clearSelect() {
                this.$refs.multipleTable.clearSelection();
            },
            handleSelectChange(val) {
                this.$emit('select', val)
            },
        },
        watch: {}
    }
</script>

<style scoped>
    .table-primary {
        white-space: nowrap;
        width: 150px;
        overflow: hidden;
        text-overflow: ellipsis;
    }
</style>