<template>
    <el-row style="padding: 25px 0">
        <el-col>

            <el-input v-model="filterList.title" style="width: 25%" size="small"
                      placeholder="搜索标题、摘要、字幕，支持中英文关键词" suffix-icon="el-icon-search"/>

            <el-select
                    v-show="listType === 'media'"
                    default-first-option
                    value=""
                    v-model="filterList.category"
                    size="small"
                    placeholder="类型（iFun iTalk..）">
                <el-option label="iFun" value="fun"/>
                <el-option label="iTalk" value="talk"/>
                <el-option label="iListen" value="listen"/>
                <el-option label="iWords" value="word"/>
            </el-select>

            <el-select placeholder="分类" v-show="listType === 'fun'" v-model="filterList.class" value="" size="small">
                <el-option
                        v-for="(index,value) in iFunTypeList"
                        :key="value"
                        :label="index"
                        :value="value">
                </el-option>
            </el-select>

            <el-select placeholder="分类" v-show="listType === 'word'" v-model="filterList.class" value="" size="small">
                <el-option
                        v-for="(index,value) in iWordTypeList"
                        :key="value"
                        :label="index"
                        :value="value">
                </el-option>
            </el-select>

            <el-select placeholder="来源" v-show="listType === 'media'"
                       v-model="filterList.source" value="" size="small">
            </el-select>

            <el-select placeholder="难度" v-model="filterList.difficulty" value="" size="small">
                <el-option label="全部" value="all"/>
                <el-option label="初级" value="1"/>
                <el-option label="中级" value="2"/>
                <el-option label="高级" value="3"/>
                <el-option label="彩蛋题" value="4" v-if="listType === 'talk'"/>
            </el-select>

            <el-select
                    multiple
                    filterable
                    allow-create
                    default-first-option
                    value=""
                    v-model="filterList.label"
                    size="small"
                    placeholder="标签">
                <el-option
                        v-for="item in labelList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"></el-option>
            </el-select>

            <el-select placeholder="状态" v-show="listType !== 'media'" v-model="filterList.status" value="" size="small">
                <el-option label="全部" value="all"/>
                <el-option label="未发布" value="1"/>
                <el-option label="待发布" value="2"/>
                <el-option label="待发布" value="3"/>
            </el-select>

            <el-button plain size="mini" @click="filterMediaList">查找</el-button>

            <el-button plain size="mini" @click="clearAll">清除筛选</el-button>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: "filter-list-item",
        props: {
            listType: String,
            filterList: Object,
            labelList: Array
        },
        data() {
            return {
                iFunTypeList: {
                    funny: '搞笑',
                    movie: '电影',
                    entertainment: '娱乐',
                    culture: '文化',
                    travel: '旅行',
                    arts: '艺术',
                    foods: '美食',
                    sports: '运动',
                    technology: '科技',
                },
                iWordTypeList: {
                    lifeStyle: '有型生活',
                    oversea: '海外生存',
                    date: '约会妙计',
                    tourGuide: '旅游指南',
                    serious: '正经一下',
                    series: '电影美剧',
                    dailyWord: '每日一词'
                },
            }
        },
        mounted() {
            // this.getAllLabelData();
        },
        methods: {
            // getAllLabelData() {
            //     this.$axios.get('/label/_find').then((res) => {
            //         console.log(res.data.data)
            //         if (!res.data.code) {
            //             this.labelList = res.data.data
            //         }
            //     })
            // },
            clearAll() {
                Object.keys(this.filterList).forEach((value) => {
                    this.filterList[value] = '';
                })
            },
            filterMediaList() {
                this.$emit('filterMediaList', this.filterList);
            },
        }
    }
</script>

<style scoped>
    .el-select {
        width: 150px;
        margin: 0 5px;
    }
</style>