<template>
    <el-aside width="201px">
        <div>
            <div @click="jumpPage('/mediaList')" class="backBlock">
                <i class="el-icon-back"></i>
                <span class="backText">返回</span>
            </div>

            <show-media :file-info="fileInfo" :current-type="currentType" style="width: 200px;height: 100px"/>

            <div class="backBlock">
                <el-button type="text" class="backBlock" @click="jumpPage('/editMediaInfo/'+$route.params.id)">基本信息</el-button>
                <el-button type="text" :disabled="currentType!=='mp4'" class="backBlock" @click="jumpPage('/editMaterialInfo/'+$route.params.id)">编辑素材</el-button>
            </div>
        </div>
    </el-aside>
</template>

<script>
    import ShowMedia from './ShowMediaItem'
    export default {
        props: {
            fileInfo: Object,
        },
        components:{
            ShowMedia,
        },
        data() {
            return {
                currentType: '',
            }
        },
        mounted() {
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
        },
        watch: {
            fileInfo(newFile) {
                this.currentType = newFile.fileUrl.substring(newFile.fileUrl.length - 3);
            },
        }
    }
</script>

<style scoped>
    .backBlock {
        padding: 20px 20px 10px;
        margin: 0;
        color: #2c3e50;
        font-size: 14px;
        cursor: pointer;
    }

    .backText {
        margin-left: 10px;
    }

    .editBlock {
        padding: 0 20px;
    }

    aside {
        width: 200px;
        height: -webkit-fill-available;
        border-right: 1px solid #e6e6e6;;
    }
</style>