<script>
    import FilterList from '../components/FilterListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'
    import LessonList from '../components/LessonListItem'

    export default {
        components: {
            FilterList,
            ReleaseTiming,
            LeftAside,
            LessonList
        },
        data() {
            return {
                iWordTypeList: {
                    lifeStyle: '有型生活',
                    oversea: '海外生存',
                    date: '约会妙计',
                    tourGuide: '旅游指南',
                    serious: '正经一下',
                    series: '电影美剧',
                    dailyWord: '每日一词',
                },
                wordsList: [],
                currentPage: 1,
                isShowSetTime: false,
                paginationNum: 0,
                paginationSize: 10,
                filterList: {},
                currentId: '',
                labelList:[],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.getWordsListData();
                this.getWordsNumData();
                this.getAllLabelData();
            },
            getWordsListData() {
                this.$axios.get('/lesson/_find', {
                    params: {
                        page: this.currentPage,
                        num: this.paginationSize,
                        category: 'iWord'
                    }
                }).then((res) => {
                    console.log(res)
                    if (!res.data.code) {
                        this.wordsList = res.data.data
                    }
                })
            },
            getWordsNumData() {
                this.$axios.get('/lesson/_getnum', {
                    params: {
                        page: this.currentPage,
                        category: 'iWord'
                    }
                }).then((res) => {
                    this.paginationNum = res.data.data;
                })
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getWordsListData();
            },
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                    this.currentId = command.value._id;
                } else {
                    this.postUpdateWordData(command.value);
                }
            },
            hideTimer() {
                this.isShowSetTime = false;
                this.init();
            },
            postUpdateWordData(command) {
                let data = new FormData();
                data.append('lessonId', command._id);
                data.append('publicTime', parseInt(new Date().getTime() / 1000));
                data.append('status', 2);

                this.$axios.post('/lesson/_update', data).then((res) => {
                    console.log(res);
                    if (!res.data.code) {
                        this.getWordsListData();
                    }
                })
            },
            getAllLabelData() {
                this.$axios.get('/label/_find', {params: {category: 'iWord', all: 'all'}}).then((res) => {
                    console.log(res.data.data)
                    if (!res.data.code) {
                        this.labelList = res.data.data
                    }
                })
            },
            delLessonInfo(id) {
                this.$confirm('文件将被直接删除', '确定删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    confirmButtonClass: 'el-button el-button--danger'
                }).then(() => {
                    this.delLessonData(id)
                }).catch(() => {
                    // this.$message({type: 'info', message: '已取消删除'});
                });
            },
            delLessonData(id) {
                this.$axios.get('/lesson/_delete', {params: {lessonId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.$message({type: 'success', message: '已删除'});
                        this.init();
                    }
                })
            },
            releaseTimer(data) {
                this.$axios.post('/lesson/_public', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('设置成功');
                        this.hideTimer();
                    }
                })
            }
        },

    }
</script>

<style scoped>
    .el-dropdown-link {
        cursor: pointer;
        color: #409eff;
        font-size: 12px
    }

    .dropdown-edit {
        margin-left: 15px
    }

    .table-secondary {
        font-size: 12px;
        color: #909399
    }
</style>