<template>
    <el-dialog
            title="定时发布"
            :visible.sync="isShow"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
            :show-close="false"
            width="40%" class="timingMain">
        <div style="min-height: 150px;">
            <el-date-picker
                    v-model="date"
                    size="small"
                    type="date"
                    style="margin-right: 15px"
                    placeholder="选择日期">
            </el-date-picker>
            <el-time-select
                    size="small"
                    v-model="time"
                    :picker-options="{start: '00:30',step: '00:30',end:'23:30'}"
                    placeholder="选择时间">
            </el-time-select>
        </div>
        <span slot="footer" class="dialog-footer">
    <el-button @click="hideTiming">取 消</el-button>
    <el-button type="primary" @click="setScheduledRelease">设 定</el-button>
  </span>
    </el-dialog>
</template>

<script>
    export default {
        props: {
            isShow: Boolean,
            id: String,
        },
        data() {
            return {
                time: '',
                date: ''
            }
        },
        mounted() {
        },
        methods: {
            hideTiming() {
                this.$emit('hideTiming', false)
            },
            setScheduledRelease() {
                let time = this.time.split(':');
                let data = new FormData();
                data.append('lessonId', this.id);
                let timestamp = new Date(this.date).setHours(time[0], time[1], 0);
                data.append('time', timestamp / 1000);

                if (this.$route.params.id === 'new') {
                    return this.$emit('hideTiming', {key: 'new', date: timestamp});
                }


                this.$emit('releaseTimer', data)
                // this.$axios.post('/lesson/_public', data).then((res) => {
                //     if (!res.data.code) {
                //         this.$message.success('设置成功');
                //         this.$emit('hideTiming', false)
                //     }
                // })
            },
        },
        watch: {
        }
    }
</script>

