<template>
    <el-container v-if="type=== 'iFun'">
        <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="width: 100%">

            <el-row>
                <el-col :span="12">
                    <el-form-item label="中文标题" prop="zhTitle">
                        <el-input placeholder="中文标题" v-model="form.zhTitle"/>
                    </el-form-item>
                    <el-form-item label="英文标题" prop="enTitle">
                        <el-input placeholder="英文标题" v-model="form.enTitle"/>
                    </el-form-item>


                    <el-form-item label="摘要" prop="abstract">
                        <el-input
                                type="textarea"
                                autosize
                                placeholder="摘要"
                                v-model="form.abstract">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="标签" prop="label">
                        <el-select value=""
                                   v-model="form.label"
                                   multiple
                                   filterable
                                   allow-create
                                   default-first-option
                                   placeholder="请选择标签">
                            <el-option
                                    v-for="item in labelList"
                                    :key="item._id"
                                    :label="item.label"
                                    :value="item.label">
                            </el-option>
                        </el-select>

                    </el-form-item>
                    <el-form-item label="中文字幕" prop="zhVvt">
                        <el-input placeholder="中文字幕" v-model="form.zhVvt"/>
                    </el-form-item>

                    <el-form-item label="英文字幕" prop="enVvt">
                        <el-input placeholder="英文字幕" v-model="form.enVvt"/>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="2">
                    <el-form-item label="分类" prop="class">
                        <el-select placeholder="分类" v-model="form.class" value="">
                            <el-option
                                    v-for="(index,value) in iFunTypeList"
                                    :key="value"
                                    :label="index"
                                    :value="value">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="难度" prop="difficulty">
                        <el-select placeholder="难度" v-model="form.difficulty" value="">
                            <el-option label="初级" value="simple"/>
                            <el-option label="中级" value="easily"/>
                            <el-option label="高级" value="difficulty"/>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </el-container>
</template>

<script>
    export default {
        props: {
            type: String,
            form: {
                type: Object,
                default() {
                    return {}
                }
            },
            labelList: Array,
        },
        data() {
            return {
                iFunTypeList: {
                    funny: '搞笑',
                    movie: '电影',
                    entertainment: '娱乐',
                    culture: '文化',
                    travel: '旅行',
                    arts: '艺术',
                    foods: '美食',
                    sports: '运动',
                    technology: '科技',
                },
                rules: {
                    zhTitle: [
                        {required: true, message: '请输入', trigger: 'blur'},
                    ],
                    enTitle: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    abstract: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    zhVvt: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    enVvt: [
                        {required: true, message: '请输入', trigger: 'blur'}
                    ],
                    class: [
                        {required: true, message: '请选择', trigger: 'change'}
                    ],
                    difficulty: [
                        {required: true, message: '请选择', trigger: 'change'}
                    ]
                }
            }
        },
        mounted() {
        },
        methods: {
            submitForm(formName) {
                return new Promise((resolve, reject) => {
                    this.$refs[formName].validate((valid) => {
                        if (valid) {
                            resolve(true)
                        } else {
                            this.$message.warning('请检查字段是否填写完整！');
                            reject(false);
                        }
                    });
                })
            },
        }
    }
</script>

<style scoped>
</style>