<template>
    <el-table
            ref=""
            :data="lessonList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="">
        <el-table-column
                type="selection"
                width="55">
        </el-table-column>
        <el-table-column
                label="标题">
            <template slot-scope="scope">{{ scope.row.title }}</template>
        </el-table-column>
        <el-table-column
                v-if="lessonType !== 'talk'"
                prop="difficulty"
                label="分类">
            <template slot-scope="scope">
                <span>{{typeList[scope.row.class]}}</span>
            </template>
        </el-table-column>
        <el-table-column
                v-if="lessonType !== 'word'"
                prop="difficulty"
                label="难度">
            <template slot-scope="scope">
                <span v-if="scope.row.difficulty === 'simple'">初级</span>
                <span v-if="scope.row.difficulty === 'easily'">中级</span>
                <span v-if="scope.row.difficulty === 'difficulty'">高级</span>
                <span v-if="scope.row.difficulty === 'bonus'">彩蛋题</span>
                <!--<span v-if="scope.row.difficulty === 'other'">其他</span>-->
            </template>
        </el-table-column>
        <el-table-column
                prop="status"
                label="状态">
            <template slot-scope="scope">
                <span v-if="scope.row.status === 0">未发布</span>
                <span v-if="scope.row.status===  1">待发布</span>
                <span v-if="scope.row.status===  2">已发布</span>
                <div class="table-secondary">{{ (scope.row.publicTime * 1000) || '' | moment("YYYY/MM/DD HH:mm")}}</div>
            </template>
        </el-table-column>
        <el-table-column
                label="操作">
            <template slot-scope="scope">
                <el-button type="text" size="small" @click="jumpPage(editPage+scope.row._id)">编辑
                </el-button>
                <el-dropdown class="dropdown-edit" trigger="click" @command="handleCommand">
                              <span class="el-dropdown-link">
                                发布<i class="el-icon-arrow-down el-icon--right"></i>
                              </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item :command="{key:'release',value:scope.row}">发布</el-dropdown-item>
                        <el-dropdown-item :command="{key:'time',value:scope.row}">定时发布</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>

                <el-button class="dropdown-edit" type="text" size="small" style="color: #F56C6C;"
                           @click="delLessonInfo(scope.row._id)">
                    刪除
                </el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
    export default {
        props: {
            lessonType: String,
            lessonList: Array,
            typeList: Object,
            editPage: String,
        },
        mounted(){
            // console.log(this.lessonType)
        },
        methods: {
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                this.$emit('handleCommand', command);
            },
            delLessonInfo(id) {
                this.$emit('delLessonInfo', id)
            },
        }
    }
</script>

<style scoped>
    .el-dropdown-link {
        cursor: pointer;
        color: #409eff;
        font-size: 12px
    }

    .dropdown-edit {
        margin-left: 15px
    }

    .table-secondary {
        font-size: 12px;
        color: #909399
    }
</style>