import Vue from 'vue'
import Router from 'vue-router'
import MediaList from './views/MediaList'
import EditMediaInfo from './views/EditMediaInfo'
import EditMaterialInfo from './views/EditMaterialInfo'
import IWordsList from './views/IWordsList'
import EditIWordInfo from './views/EditIWordInfo'
import ITalkList from './views/ITalkList'
import ShowITalkInfo from './views/ShowITalkInfo'
import IFunList from './views/IFunList'

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'MediaList',
            component: MediaList
        },
        {
            path: '/mediaList',
            name: 'MediaList',
            component: MediaList
        },
        {
            path: '/editMediaInfo/:id',
            name: 'EditMediaInfo',
            component: EditMediaInfo
        },
        {
            path: '/editMaterialInfo/:id',
            name: 'EditMaterialInfo',
            component: EditMaterialInfo
        },
        {
            path: '/iWordsList',
            name: 'IWordsList',
            component: IWordsList
        },
        {
            path: '/editIWordInfo/:id',
            name: 'EditIWordInfo',
            component: EditIWordInfo
        },
        {
            path: '/iTalkList',
            name: 'ITalkList',
            component: ITalkList
        },
        {
            path: '/showITalkInfo/:id',
            name: 'ShowITalkInfo',
            component: ShowITalkInfo
        },
        {
            path:'/iFunList',
            name: 'IFunList',
            component: IFunList
        },
        {
            path:'/showIFunInfo/:id',
            name: 'ShowIFunInfo',
            component: ShowITalkInfo
        }
    ]
})
